*,
*::before,
*::after{
  box-sizing: border-box;
  margin: 0;
  padding:0;
}

body{
  background-color:#333;
}

input{
  font:inherit;
  display: block;
  width: 100%;
  height: 100%;
  text-indent: 1em;
}

img{
  display:block;
  max-width:100%;
  height:auto;
  border-radius: 5px;
}

.wrapper{
  max-width: 1080px;
  margin: 0 auto;
  min-height: 100vh;
  background-color:aliceblue;
}

.nav{
  background: linear-gradient(to right,#672280, #A626D3);
  color:#eee;
  padding: 1.5em;
  text-align: center;
}

.main{
  padding: 3em;
  display:grid;
  grid-template-rows: 3em 3em auto;
  grid-template-columns: 1fr 1fr;
  gap: 2em;
}

.top-text-field{
  grid-column: 1 / 2;
  grid-row:span 1;
}

.bottom-text-field{
  grid-column: 2 / 3;
  grid-row:span 1;
}

.generate-meme-btn{
  grid-column: 1 / 3;
  cursor: pointer;
  background: linear-gradient(to right,#672280, #A626D3);
  font:inherit;
  font-weight: bold;
  color: #eee;
  border:none;
  border-radius:3px;
  transition:all .3s;
}

.meme{
  grid-column: 1 / 3;
  display: flex;
  justify-content: center;
  align-items:start;
  position: relative;
}

.generate-meme-btn:hover{
  transform:translateY(-2px);
}

.meme-top-text, 
.meme-bottom-text{
  position: absolute;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.meme-top-text{
  left: 50%;
  top: 1em;
  transform:translateX(-50%);
}

.meme-bottom-text{
  left: 50%;
  bottom: 1em;
  transform:translateX(-50%);
}

.meme-error{
  text-align: center;
  grid-column: 1 / 3;
}